import React, { Component } from 'react';
import * as axios from 'axios';

export default class Contact extends Component {
  constructor() {
    super()
    this.state = {
      name: '',
      email: '',
      message: '',
    }
  }

  handleInputChange = (event) => this.setState({ [event.target.attributes.type.value]: event.target.value });

  handleSubmit = (event) => {
    event.preventDefault();
    const { name, email, message } = this.state;
    axios.post('/api/sendEmail', 
    {
      name,
      email,
      message
    })
    .then(result => this.setState({name: '', email: '', message: ''}))
    .catch(error => console.log('server response email error', error));
  }

  render() {
    const { name, email, message } = this.state;
    return (
      <div className="row">
        <div className="col d-flex flex-column justify-content-center align-items-center" style={{ minHeight: '100vh' }} id="contact">
          <form className="shadow p-5 contact-form" onSubmit={this.handleSubmit}>
            <h2 className="text-center pb-0 mb-0">Contact Me</h2>
            <p className="text-secondary text-center p-1 m-1">I'll get back to you ASAP</p>
            <div className="form-group">
              <label className="text-left">Name</label>
              <input type="name" placeholder="Name..." class="form-control" value={name} onChange={this.handleInputChange} />
            </div>
            <div className="form-group">
              <label className="text-left">Email</label>
              <input type="email" placeholder="Email..." class="form-control" value={email} onChange={this.handleInputChange} />
            </div>
            <div className="form-group">
              <label className="text-left">Message</label>
              <textarea rows="5" type="message" placeholder="Email..." class="form-control" value={message} onChange={this.handleInputChange} />
            </div>
            <button className="btn btn-primary">Submit</button>
          </form>
        </div>
      </div>
    );
  }
}